<template>
  <div>
    <Loadings v-if="list ==null"></Loadings>
    <div class="container scontainer" v-else>
      <div class="search-bar">
        <van-search
          v-model="keywords"
          show-action
          label="查询"
          placeholder="请输入搜索关键词"
          @search="searchData"
          clearable
        >
          <template #action>
            <div @click="searchData">搜索</div>
          </template>
        </van-search>
      </div>
      <Empty v-if=" list.length == 0"></Empty>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        class="list"
        v-else
      >
        <router-link
          :to="{ path: '/InvoiceDetails/'+item.invoice_no }"
          class="item"
          v-for="(item, index) in list"
          :key="index"
        >
          <div class="title flex-sb">
            {{item.cust_name}}
            <span class="time">{{formatDate(item.op_time)}}</span>
          </div>

          <div class="info">
            <div>发货单号: {{item.fhdh_no}}</div>
            <div>客户电话: {{item.oi_phone}}</div>

            <div>业务员: {{item.name}}</div>
            <div>操作员: {{item.optioner}}</div>
          </div>

          <div class="price-num flex-sb">
            <p>数量</p>
            {{item.num}}{{item.unit ? item.unit : '件'}}
          </div>
        </router-link>
      </van-list>
    </div>
  </div>
</template>

<script>
import { invoice } from "@api/public";

import { Icon, Search, Loading, List } from "vant";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
export default {
  name: "Shipmentuery",
  components: {
    [Icon.name]: Icon,
    [Search.name]: Search,
    [Loading.name]: Loading,
    [List.name]: List,
    Empty,
    Loadings,
  },

  data: function () {
    return {
      list: null,
      keywords: "",
      page: "",
      loading: false,
      finished: false,
    };
  },
  created() {},
  mounted: function () {
    this.getData();
  },
  methods: {
    searchData() {
      this.page = "";
      this.getData();
    },
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
    getData() {
      invoice({
        search: this.keywords,
        page: this.page,
  
      }).then((res) => {
        if (this.page != "") {
          this.list = this.list.concat(res.data.data);
        } else {
          this.list = res.data.data;
        }

        this.finished = res.data.data.length < 200 ? true : false;
        this.loading = res.data.data.length < 200 ? true : false;
        if (res.data.data.length == 200) {
          this.page = this.list[this.list.length - 1].oi_id;
        }
      });
    },
    onLoad() {
      this.getData();
    },
  },
};
</script>
<style scoped>
.container {
  box-shadow: 0 0 5px #eee;
  background: #fff;

  border-radius: 5px;
}

.container .caption {
  font-weight: 600;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #333;
}

.caption .month {
  height: 25px;
  width: 60px;
  background: #092e48;
  border-radius: 20px;
  color: #fff;
  line-height: 25px;
  font-size: 12px;
}

.caption .month span {
  display: inline-block;
  width: 23px;
  height: 23px;
  background: #fff;
  border-radius: 50%;
  margin-left: 1px;
  margin-right: 4px;
}

.container .list {
  line-height: 43px;
  font-size: 14px;
}

.container .list .item {
  margin-bottom: 10px;
  padding: 15px 8px 0;
  display: block;

  border-bottom: 1px solid #eee;
}

.container .list > div {
  background-color: #fff;
  margin-bottom: 10px;
}

.container .list img {
  width: 80px;
  margin-right: 10px;
}

.container .list .other {
  width: calc(100% - 90px);
}

.container .list .info {
  line-height: 18px;
  color: #999;
  font-size: 12px;
}

.item .title {
  line-height: 25px;
  font-size: 14px;
  color: #333;
}

.container .list .item .right {
  font-weight: bold;
}

.container .list .item .right span {
  font-size: 10px;
  font-weight: 500;
}

.price-num {
  text-align: center;
  font-size: 12px;
  padding-bottom: 10px;
  color: #999;

  line-height: 20px;
}

.time {
  font-size: 12px;
  color: #999;
}
</style>
